        window.open('https://test.dreamcity.studio/sugar', '_parent');
      }
      else if (artist == '兔乃') {
        window.open('https://www.dreamcity.studio/skin/', '_parent');
      }
    },
    ShowOverlay(ind, index) {
      this.show_img = this.big_list[ind][index];
      this.ov = !this.ov;
    },
